import React, { Component } from "react";
import {Container, Card} from "react-bootstrap";
import { setIssues } from "../actions";
import { connect } from "react-redux";
import Head from "../components/head/head";
import Issues from "../components/issues/issues"

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      title: "é¦–é¡µ",
      description: "è¿™é‡Œæ˜¯æ˜¾ç¤ºå†…å®¹çš„ä¸»é¡µ",
      message: "test",
      spaces: [],
      newPages:[],
      lastPageInfo: {},
    };
  }
  componentDidMount() {
    let pageInfo = { endCursor: null, hasNextPage: true  }
    localStorage.setItem("pageInfo",JSON.stringify(pageInfo))
    localStorage.setItem("issuesLodding",false)
    window.addEventListener('scroll',this.scrollNew)
    // console.log(this.state.description)
    this.getNewPage();
  }
  componentWillUnmount (){
    window.removeEventListener('scroll',this.scrollNew)
  }
  scrollNew = ()=>{
    let scrollHeight = document.documentElement.scrollHeight
    let scrollTop = document.documentElement.scrollTop
    let clientHeight = document.documentElement.clientHeight
    let lodding = localStorage.getItem("issuesLodding")
    // console.log("height",scrollHeight,scrollTop,clientHeight)
    if(scrollTop + clientHeight > scrollHeight - 2&&lodding!=="lodding"){
      // å•çº¯çš„ç›¸ç­‰çš„æƒ…å†µä¼šå‡ºç° æµ®ç‚¹æ•°çš„æƒ…å†µï¼Œå°±ä¸èƒ½æ‰§è¡Œ
      this.getNewPage()
      // console.log("æ–°ä¸€é¡µ")
      localStorage.setItem("issuesLodding","lodding")
    }
  }
  getNewPage = ()=>{
    // window.removeEventListener("scroll",this.scrollNew) // å› ä¸ºå¥‡è‘©çš„é—®é¢˜å¯¼è‡´å†…å®¹é‡å¤é—®é¢˜
    let pageInfo = JSON.parse(localStorage.getItem("pageInfo"))
    let after = pageInfo.endCursor
    let variables = this.props.state.setConfig.variables
    variables.after = after
    if(!pageInfo.hasNextPage){
      alert("æ²¡æœ‰æ›´å¤šäº†")
      window.removeEventListener("scroll",this.scrollNew)
      return 
    }
    let key = pageInfo.endCursor
    let newPage = (<Issues 
      key = {key}
      variables={variables} 
      props={{...this.props}}
      getNextPage={this.getNextPage} 
      
      />    )
    let newPages = this.state.newPages
    newPages.push(newPage)
    // console.log(newPages)
    this.setState({
      newPages,
    })
  }
  render() { 
    return (
      <Container className="xx-space">
        <Head {...this.props} />
        <Card border="light"> 
            {this.state.newPages.map(item=>item)}
        </Card>
      </Container>
    );
  }
}

// mapStateToPropsï¼šå°†stateæ˜ å°„åˆ°ç»„ä»¶çš„propsä¸­
const mapStateToProps = state => {
  console.log("home-state æ˜ å°„", state);
  return {
    state
  };
};
// mapDispatchToPropsï¼šå°†dispatchæ˜ å°„åˆ°ç»„ä»¶çš„propsä¸­
const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    setIssues(data) {
      dispatch(setIssues(data));
    }
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Home);
