import defaultState from '../state.js'
import * as Types from '../../actions/Types'
function setIssue(state = {},action){
    // console.log("setIssue Type:",action.type)
    switch(action.type){
        case Types.SET_ISSUE:
            // state.issue = action.data
            //console.log("å¾—åˆ°",state,"data",action,action.type)
            //return state
            // console.log("action.data",action.data,state,typeof state)
            state = action.data
            return Object.assign({}, state) //ç›´æ¥è¿”å›stateæ˜¯ä¸ä¼šè‡ªåŠ¨çš„åˆ·æ–°çš„
        default:
            return state
    }
}
function setIssues(state = {},action){
    // console.log("setIssues Type:",action.type)
    switch(action.type){
        case Types.SET_ISSUES:
        state.issues = action.data
        console.log("å¾—åˆ°",state,"data",action,action.type)
        //return state
        return Object.assign({},state)
        default:
        return state
    }
}
function setConfig (state = {},action){
    switch(action.type){
        case Types.SET_CONFIG:
            // console.log("è®¾ç½®é…ç½®", action.data)
            state.config = action.data
            state.client = action.data.client
            state.variables = {
                owner: action.data.owner,
                name: action.data.name,
                first: 10,
                after: null
            }
            return Object.assign({}, state)
        case Types.SET_PAGEINFO:
            state.pageInfo = state.data
            return Object.assign({}, state)
        case Types.SET_NEXTPAGE:
            state.variables.after = action.data
            // console.log("è®¾ç½®ä¸‹ä¸€é¡µçš„afterå˜é‡")
            return Object.assign({}, state)
        default:
        //console.log("å¾—åˆ°",state,"data",action,action.type)
        return state
    }
}
function pageTitle (state = defaultState.pageTitle,action) {
// ä¸åŒçš„actionæœ‰ä¸åŒçš„å¤„ç†é€»è¾‘
    switch (action.type) {
        case 'SET_PAGE_TITLE':
        return action.data
        default:
        return state
    }
}

function user (state = defaultState.user, action){
    switch (action.type) {
        case 'SET_USER':
        return action.data
        default:
        return state
    }
}

export {
    setIssue,
    setIssues,
    setConfig,
    pageTitle,
    user
}